/* Colors */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0 auto;
  padding: 0;
  overflow-x: hidden;
  font-family: "Montserrat", "Arial", sans-serif;
}
a {
  text-decoration: none;
}
.main {
  position: relative;
  padding-top: 149px;
}
.main::before {
  content: "";
  position: absolute;
  top: 186px;
  right: 0;
  display: block;
  width: 714px;
  height: 611px;
  background-image: url("../img/main_background@1x.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.main::after {
  content: "";
  position: absolute;
  top: -245px;
  right: -45px;
  z-index: -10;
  display: block;
  width: 550px;
  height: 800px;
  background: repeating-linear-gradient(black, black 80px, transparent 80px, transparent 160px);
  transform: rotate(45deg);
  animation: background 4s infinite;
  animation-timing-function: linear;
}
@keyframes background {
  100% {
    background-position: 0 -800px;
  }
}
.main__top {
  padding-left: 7px;
  margin-bottom: 53px;
}
.main__content {
  margin-bottom: 117px;
}
.main__footer-button {
  width: 210px;
  height: 65px;
  margin: 0 21px 80px 0;
  border-radius: 12px;
}
.container {
  width: 88.6%;
  max-width: 1240px;
  margin: 0 auto;
}
.container--flex {
  display: flex;
  flex-wrap: wrap;
}
.content__slogan {
  display: block;
  width: 462px;
  margin-bottom: 21px;
  font-size: 50px;
  font-weight: 700;
  line-height: 60px;
  color: #000000;
}
.content__text {
  width: 505px;
  margin: 0 0 8px;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  color: #000000;
}
.button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  border: none;
  cursor: pointer;
}
.button--accent {
  color: #000000;
  background-color: #fbcb03;
}
.button--accent:hover,
.button--accent:focus {
  background-color: rgba(251, 203, 3, 0.8);
}
.button--dark {
  color: #ffffff;
  background-color: #000000;
}
.button--dark:hover,
.button--dark:focus {
  background-color: rgba(0, 0, 0, 0.8);
}
.button--icon-hi .button__icon {
  top: -49px;
}
.button--icon-facepalm .button__icon {
  top: -41px;
}
.button__icon {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}
